import { Component, OnInit } from '@angular/core';

import {
  Card,
  cardCollection
} from 'dice-guardians-module'

import { DiceGuardiansModuleService } from '../../services/dice-guardians-module.service'


@Component({
  selector: 'app-card-viewer',
  templateUrl: './card-viewer.page.html',
  styleUrls: ['./card-viewer.page.scss'],
})
export class CardViewerPage implements OnInit {
  isSingleCard:boolean = true
  cards:Array<Card> = []

  lib:DiceGuardiansModuleService


  constructor(
    lib:DiceGuardiansModuleService
  ) {
    this.lib = lib
  }

  ngOnInit() {
    const self = this
    this.getCard()

    window.addEventListener('hashchange', () => {
      self.getCard()
    }, false)
  }

  getCard() : Card{
    const hash = window.location.hash.substr(1)
    const card = cardCollection[hash]
    this.cards = []

    if (card) {
      this.isSingleCard = true
      return card
    } else {
      this.isSingleCard = false
      const names = Object.keys(cardCollection)
      for (let i = 0; i < names.length; i++){
        this.cards.push(cardCollection[names[i]])
      }
      return this.cards[0]
    }
  }

  viewCard(card:Card){
    const names = Object.keys(cardCollection)
    for (let i = 0; i < names.length; i++){
      const collectionCard = cardCollection[names[i]]
      console.log(card, collectionCard)
      if (collectionCard.title === card.title){
        window.location.href = '/card-viewer#' + names[i]
        //window.location.reload()
      }
    }
  }

  viewAll(){
    window.location.hash = ''
    //window.location.reload()
  }

  getFullscreenWidth(){
    const height = document.querySelector('.grid').clientHeight
    return (height / 1.5) - 50

  }
}
